export default {
	data() {
		return {
			id: 0,
			mainData: {
				title: "",
				create_time: null,
				content: ""
			}
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		if (option.id) this.id = parseInt(option.id);
		this.getInfo();
	},
	methods: {
		getDate(item) { // 时间戳转日期
			var time = new Date(item)
			var year = time.getFullYear()
			var month = time.getMonth() + 1
			var date = time.getDate()
			var hours = time.getHours()
			var minutes = time.getMinutes()
			return year + '-' + this.add0(month) + '-' + this.add0(date) + " " + this.add0(hours) + ":" + this.add0(
				minutes)
		},
		add0(m) {
			return m < 10 ? '0' + m : m
		},
		getInfo() {
			//获取材料类别
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.news
				.articleDetails({
					article_id: _this.id
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						_this.mainData = data.data;
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
